import React from 'react';
import PropTypes from 'prop-types';
import ScrollToNext from '@components/ScrollToNext';
import './style.scss';
import Slide from 'react-reveal/Slide';
import Typing from 'react-typing-animation';
import Button from '@material-ui/core/Button';

const AboutPage = (props, context) => {
  const {
    theme: { colorPrimary, colorHighlight, bgPrimary, textPrimary }
  } = context;

  return (
    <div className="about-page" style={{ backgroundColor: bgPrimary }}>
      <style jsx="true">
        {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
      </style>
      <div className="content-grid">
      <Slide right>
        <h1 style={{ color: colorPrimary }}>About</h1>
      </Slide>
        <div className="about-wrapper">
          <div className="about-content" style={{ color: textPrimary }}>
            <p>
              Suite à ma reconversion professionnelle en 2019, j'ai décidé de me perfectionner afin d'obtenir un <strong>titre professionnel RNCP BAC +3 "Concepteur Designer UI"</strong> ainsi qu'un <strong>certificat Opquast "Maitrise de la qualité en projet web"</strong>
            </p>
            <p>
              Actuellement dans une formation chez WebForce3 à Lyon en tant que <strong>Chargé de Projet digital</strong> , <span style={{fontStyle: 'italic'}}>disponible Janvier 2022</span> , après avoir compléter mes compétences pour mettre en oeuvre et développer des actions de communication à destination de réseaux sociaux, pour une nouvelle opportunité de travail.
            </p>
            <p>
            Les 3 principaux points abordés : 
              <div  style={{ marginLeft: 50}} >
                <li>
                Conception d'un support web design et video
                </li>
                <li>
                Gestion de Projet de communication numérique
                </li>
                <li>
                Evoluer la partie Front du website
                </li>
              </div>
            </p>
            <p>
              Toujours chez LDLC-Event en Freelance à Lyon en tant que Front-End Developer pour les sites LDLC-OL et d4gamers</p>
            {/* <p>Passionné et curieux, j'aime découvrir et tester de nouveaux concepts. Dernier projet mise en ligne fin mai 2020 : le site vitrine <a target="_blank" rel="noopener noreferrer" href="https://ldlc-ol.com"><span className="highlight">LDLC-OL</span></a>.</p>
            <p>  Voici ce que j'utilise actuellement comme stack : <div>
                <Typing speed={250}><i>React.js, Antd, Node.js, dva, axios, echarts-for-react, Express.js, MongoDB, Wordpress.</i>
                </Typing></div>
            </p> */}
            <p className="text-emoji" style={{ color: colorPrimary }}>
              <Button variant="outlined" size="medium" color="primary" target="_blank" style={{ marginRight: 20 }} href="https://drive.google.com/open?id=1siLkkpFFqINw83K7utN7OM6ADxe9TaQV" >
          Mon CV
        </Button>
              <Button variant="outlined" size="medium" color="primary" target="_blank" href="https://drive.google.com/file/d/0BwFfsH91yIATWC1oU2xLSXRlclRsaFl2angtV3lmTkpBay1v/view?usp=sharing" >
          Certificat La Capsule
        </Button>
            </p>
            {/* <Typing speed={50}>
            PS : Merci à Noël, Benjamin, Franck et Alexis @ La Capsule Academy !
            </Typing> */}
          </div>
        </div>
      </div>
      <ScrollToNext pageSelector=".portfolio-page" />
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any
};

export default AboutPage;



